      border-right: 1px solid #fff; } }
  .main-navigation ul li a,
  .main-navigation p {
    padding-right: 0.7rem; }
  .main-navigation p {
    margin-bottom: 0;
    font-weight: lighter;
    color: #fff;
    padding: 0;
    display: flex;
    margin-top: 0.8rem; }
    @media (min-width: 600px) {
